import D from '../../';
import { default as parent, transform } from '../Object';

const cls = class Function extends parent {
	constructor(func = () => {}){
		super(func);
	}
};

D.Function = cls;
D.constructors.unshift({
	check: D.isFunction,
	cls
});

export default cls;


