import { assign } from '../../libs/assign';

const methods = {
	isArray(array){
		return Array.isArray(array);
	},
	isBoolean(boolean){
		return typeof boolean === 'boolean' || boolean instanceof Boolean;
	},
	isDate(date){
		return date instanceof Date;
	},
	isFunction(func){
		return typeof func === 'function';
	},
	isNaN(nan){
		return nan !== nan;
	},
	isNumber(number){
		return Object.prototype.toString.call(number) === '[object Number]';
	},
	isNull(nul){
		return nul === null;
	},
	isNullOrUndefined(nul){
		return nul === null || typeof nul === 'undefined';
	},
	isObject(object){
		return !!object && (typeof object === 'object' || object instanceof Object);
	},
	isRegexp(regexp){
		return regexp instanceof RegExp;
	},
	isString(string){
		return Object.prototype.toString.call(string) === '[object String]';
	},
	isUndefined(undef){
		return typeof undef === 'undefined';
	}
};

assign(methods, {
	isDateAlike(date){
		date = new Date(date);
		return date.toString() !== 'Invalid Date';
	},
	isInteger(integer){
		return methods.isNumber(integer) &&
			integer % 1 === 0;
	},
	isNumberAlike(number){
		if (methods.isNaN(number) || number === 'NaN') {
			return true;
		}
		number = Number(number);
		return !!(number || number === 0);
	}
});

assign(methods, {
	isArrayAlike(array){
		if (!array || methods.isFunction(array)) {
			return false;
		}

		const length = array.length;

		return methods.isInteger(length) && length >= 0;
	},
	isIntegerAlike(integer){
		integer = parseInt(Number(integer));
		return !!(integer || integer === 0);
	}
});

export default methods;


