import D from '../../';
import methods from '../../methods';
import { default as parent, transform } from '../Object';
import { validate } from '../../libs';

const NativeString = String;

const cls = class String extends parent {
	constructor(string = '') {
		super(NativeString(string));
	}

	alphabet() {
		const ranges = this.$.match(/[\s\S]-[\s\S]/g) || [],
			length = ranges.length,
			alphabet = [];

		for (let i = 0; i < length; i++) {
			const range = ranges[i],
				start = range.charCodeAt(0),
				end = range.charCodeAt(2);

			if (start > end) {
				throw new Error('Start of the range must be before its end!');
			}

			for (let k = 0, len = end - start + 1; k < len; k++) {
				alphabet.push(NativeString.fromCharCode(start + k));
			}
		}

		return new D.Alphabet(alphabet);
	}
	// TODO: .endsWith()
	// TODO: .in()
	indexOf() {
		return this.$.indexOf.apply(this.$, arguments);
	}
	match() {
		const match = this.$.match.apply(this.$, arguments);

		if (methods.isArray(match)) {
			return new D.Array(match);
		}

		return new D.Object(match);
	}
	// TODO: .parse(string('json' | 'html' | 'xml'?))
	repeat(n) {
		validate([n], { 0: ['intAlike', '>=0'] });

		n = Number(n);

		let string = this.$,
			s = '';

		for (let i = 0; i < n; i++) {
			s += string;
		}

		return new String(s);
	}
	replace(regexp, replacer = '') {
		return new String(this.$.replace(regexp, replacer));
	}
	replaceString(string, replacer = '') {
		validate([string], { 0: 'string' });

		return new String(this.$.split(string).join(replacer));
	}
	split(delimiter) {
		return new D.Array(this.$.split(delimiter));
	}
	// TODO: .startsWith()
	// TODO: .substring()
	// TODO: .substr()
	toCamelCase() {
		return new String(this.$
			.replace(/( |-|_|\.)[^ \-_\.]/g, (match) => {
				return match[1].toUpperCase();
			})
			.replace(/\s+/g, '')
		);
	}
	toDotCase() {
		return new String(this.$
			.replace(/( |-|_)[^ \-_]/g, (match) => {
				return '.' + match[1].toLowerCase();
			})
			.replace(/\s+/g, '')
			.replace(/^[\s\S]/, (match) => {
				return match.toLowerCase();
			})
			.replace(/[\s\S]/g, (match) => {
				if (match.toLowerCase() !== match) {
					return '.' + match.toLowerCase();
				}

				return match;
			})
		);
	}
	toLowerCase() {
		return new String(this.$.toLowerCase());
	}
	toSnakeCase() {
		return new String(this.$
			.replace(/( |-|\.)[^ \-\.]/g, (match) => {
				return '_' + match[1].toLowerCase();
			})
			.replace(/\s+/g, '')
			.replace(/^[\s\S]/, (match) => {
				return match.toLowerCase();
			})
			.replace(/[\s\S]/g, (match) => {
				if (match.toLowerCase() !== match) {
					return '_' + match.toLowerCase();
				}

				return match;
			})
		);
	}
	toSpaceCase() {
		return new String(this.$
			.replace(/^\s+/, '')
			.replace(/\s+[\S]/g, (match) => {
				if (match.toLowerCase() !== match) {
					return match.substr(-1);
				}

				return match.substr(-2);
			})
			.replace(/(-|_|\.)[^\-_\.]/g, (match) => {
				return ' ' + match[1].toLowerCase();
			})
			.replace(/^[\s\S]/, (match) => {
				return match.toLowerCase();
			})
			.replace(/[\s\S]/g, (match) => {
				if (match.toLowerCase() !== match) {
					return ' ' + match.toLowerCase();
				}

				return match;
			})
		);
	}
	toSpinalCase() {
		return new String(this.$
			.replace(/( |_|\.)[^ \-_]/g, (match) => {
				return '-' + match[1].toLowerCase();
			})
			.replace(/\s+/g, '')
			.replace(/^[\s\S]/, (match) => {
				return match.toLowerCase();
			})
			.replace(/[\s\S]/g, (match) => {
				if (match.toLowerCase() !== match) {
					return '-' + match.toLowerCase();
				}

				return match;
			})
		);
	}
	toString() {
		return NativeString(this.$);
	}
	toUpperCase() {
		return new String(this.$.toUpperCase());
	}
	trim(side) {
		if (side === 'left') {
			return new String(this.$.replace(/^\s+/, ''));
		}

		if (side === 'right') {
			return new String(this.$.replace(/\s+$/, ''));
		}

		return new String(this.$.replace(/^\s+|\s+$/g, ''));
	}
};

D.String = cls;
D.constructors.unshift({
	check: methods.isString,
	cls
});

export default cls;


